<template>
  <div class="cjwt_wrap">
 <headers :title="title"></headers>
    <div class="zghh">
     <p>专供好物</p>
     <ul >
       <li v-for="obj in aboutData" :key="obj.id"><van-icon id="icon" name="pending-payment" /><span>{{obj.name}}</span></li>
       <!-- <li><van-icon id="icon" name="pending-payment" /><span>支付问题</span></li>
       <li><van-icon id="icon" name="logistics" /><span>物流问题</span></li>
       <li><van-icon id="icon" name="pending-orders" /><span>订单问题</span></li>
       <li><van-icon id="icon" name="tosend" /><span>商品问题</span></li> -->
     </ul>
    </div>
  </div>
</template>
<script>
  import "lib-flexible"
  import headers from '../components/editHeader.vue'
  export default {
    data() {
      return {
        title: "常见问题",
         aboutData:[]
      };
    },
    components: {
      headers
    },
    created () {
      let _this = this;
      this.$post('guanjia_heard/shopping/manage/getAbout',{'aboutType': 2}).then((res)=>{
        console.log(res)
        _this.aboutData = res.about
      })
    },

  };
</script>
<style scoped>
  .cjwt_wrap {
    flex: 1;
    background: #F8F8FF;
  }

  .zghh {
    background: #fff;
    width: 95%;
    margin: 110px auto;
    border-radius: 10px;
  }

  .zghh ul {
    list-style: none;
    overflow: hidden;
    width: 88%;
    margin: 84px auto 0;
    display: flex;
    justify-content: space-around;
  }

  .zghh ul li {
    text-align: center;
    display: flex;
    flex-direction: column;
  }

  .zghh p {
    font-size: 32px;
    padding: 38px 20px;
    font-weight: bold;
  }

  #icon {
    font-size: 66px;
    color: orange;
  }

  .zghh span {
    margin-top: 36px;
    font-size: 24px;
    font-weight: bold;
    padding-bottom: 44px;
  }
</style>



